<template>
	<div>
		<el-breadcrumb separator-class="el-icon-arrow-right">
		  <el-breadcrumb-item :to="{ path: '/admin/main' }"></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<el-card class="box-card">
			<div>
				<el-input 
							v-model.trim="text" 
							placeholder="" 
							style="width: 220px; margin-left: 10px;"
							@blur="showContent"
						 >
						 </el-input>
			</div>
			<div style="margin-top: 15px;">
				
				<article v-html="this.render1" style="margin-top: 10px;"/>
				<!-- 	<el-input
					    type="textarea"
						:rows="15"
						placeholder=""
						v-model.trim="textarea"
						resize="none"
					>
					</el-input> -->
			</div>
			<div style="margin-top: 15px;">
				<br />
				<!-- :toolbars = "toolbars" -->
				<mavon-editor 
					v-model="textarea"
					ref="md"
					@change="changeData"
				/>
			</div>
		<!-- 	<div style="margin-top: 20px;">
				<el-switch
							v-model="value"
							active-color="#0000FF"
							inactive-color="#CCCCCC"
				         >
						 </el-switch>
			</div> -->
			<div class="button-group">
				<el-button type="primary" @click="resetInfo"></el-button>
				<el-button type="primary" style="margin-left: 20px;" @click="submitInfo"></el-button>
			</div>
		</el-card>
	</div>
</template>

<script>
	export default{
		name: 'Content',
		data(){
			return{
				// 
				text: '',
				// text
				textarea: '',
				// html
				render: '',
				// 
				render1: '',
				// id
				id: 0,
				value: false,
				// toolbars: {
				// 	bold: true, // 
				//    italic: true, // 
				//    header: true, // 
				//    underline: true, // 
				//    strikethrough: true, // 
				//    mark: true, // 
				//    superscript: true, // 
				//    subscript: true, // 
				//    quote: true, // 
				//    ol: true, // 
				//    ul: true, // 
				//    link: true, // 
				//    imagelink: true, // 
				//    code: true, // code
				//    table: true, // 
				//    fullscreen: true, // 
				//    readmodel: true, // 
				//    htmlcode: true, // html
				//    help: true, // 
				//    /* 1.3.5 */
				//    undo: true, // 
				//    redo: true, // 
				//    trash: true, // 
				//    save: true, // eventssave
				//    /* 1.4.2 */
				//    navigation: true, // 
				//    /* 2.1.8 */
				//    alignleft: true, // 
				//    aligncenter: true, // 
				//    alignright: true, // 
				//    /* 2.2.1 */
				//    subfield: true, // 
				//    preview: true, // 					
				// }
				// toolbars: {
				// 	bold: true, // 
				// 	italic: true, // 
				// 	header: true, // 
				// 	underline: true, // 
				// 	mark: true, // 
				// 	superscript: true, // 
				// 	quote: true, // 
				// 	ol: true, // 
				// 	link: true, // 
				// 	imagelink: true, // 
				// 	help: true, // 
				// 	code: true, // code
				// 	subfield: true, // 
				// 	fullscreen: true, // 
				// 	readmodel: true, // 
				// 	/* 1.3.5 */
				// 	undo: true, // 
				// 	trash: true, // 
				// 	save: true, // eventssave
				// 	/* 1.4.2 */
				// 	navigation: true, // 
				// }
			}
		},
		methods:{
			resetInfo(){
				this.text = ''
				this.textarea = ''
				this.render1 = ''
				this.render = ''
			},
			// 
			async submitInfo(){
				if(this.text == ''){
					this.$message.error('')
					return
				}
				else if(this.textarea == ''){
					this.$message.error('')
					return
				}
				
				//  id  render1 
				//  id  0
				else if(this.id == 0){
					this.$message.error('')
				}
				//  id  0render1 
				else if(this.id != 0 && this.render1 == ''){
					// console.log('')
					let data = {
						blog_detail_id: this.id,
						blog_content: this.render
					}
					const res = await this.$http.post('/blogAdminContent/insertBlogAdminContentByDetailId',data)
					if(res.data = 1 && res.status == 200){
						this.$message.success('')
						
						// render1
						let params = {
							blog_title: this.text
						}
						const res1 = await this.$http.get('/blogAdminContent/selectBlogAdminContentByTitle',{params: params})
						this.render1 = res1.data
						
						return
					}
					// console.log(res,'')
					
				}
				// id 0render1
				else if(this.id != 0 && this.render1 != ''){
					// console.log('')
					let data = {
						blog_title: this.text,
						blog_content: this.render
					}
					const res = await this.$http.post('/blogAdminContent/updateBlogAdminContentByTitle',data)
					if(res.data == 1 && res.status == 200){
						this.$message.success('')
						
						// render1
						let params = {
							blog_title: this.text
						}
						const res1 = await this.$http.get('/blogAdminContent/selectBlogAdminContentByTitle',{params: params})
						this.render1 = res1.data
						
						return
					}
					// console.log(res,'')
				
				}
				
			},
			// 
			async showContent(){
				// console.log('')
				// 
				
				let params = {
					blog_title: this.text
				}
				const res = await this.$http.get('/blogAdminContent/selectBlogAdminContentByTitle',{params: params})
				this.render1 = res.data
				// console.log(this.render1 == '')
				
				//   id
				const res1 = await this.$http.get('/blogAdminContent/selectBlogAdminDetailIdByTitle',{params: params})
				this.id = res1.data
			},
			// html
			changeData(value,render){
				// valuerenderhtml
				// console.log(value)
				// console.log(render)
				this.render = render
			}
		}
	}
</script>

