	.el-breadcrumb{
		// background-color: yellow;
		width: 500px;
		margin-left: 45px;
		margin-top: 20px;
	}
	.box-card{
		width: 1200px;
		margin: 30px auto;
		
		// .el-textarea{
		// 	width: 800px;
		// 	margin-top: 10px;
		// 	margin-left: 90px;
		// }
		
		.v-note-wrapper{
			margin-top: 20px;
		}
		
		.el-switch{
			 margin-left: 10px;
		}
		
		.button-group{
			  // background-color: yellow;
			  margin-top: 30px;
			  margin-left: 140px;
		}
	}
</style>