<template>
	<div class="detail-main">
		<el-breadcrumb separator-class="el-icon-arrow-right">
		  <el-breadcrumb-item :to="{ path: '/admin/main' }"></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<el-card class="box-card">
			<el-button type="primary" @click="addBlogDetail"></el-button>
			<el-input v-model.trim="title" placeholder="" style="width: 220px; margin-left: 775px;"></el-input>
			<el-button type="primary" icon="el-icon-search" style="margin-left: 10px;" @click="search"></el-button>
			<el-table
			    :data="tableData"
				class="box-table"
			    border
			    style="width: 100%"
			>
				<el-table-column
				    label="#"
					type="index"
					width="50"
				>
				</el-table-column>
				 <el-table-column
					prop="blog_title"
					label=""
					width="180"
				>
				 </el-table-column>
				  <el-table-column
					prop="blog_preface"
					label=""
					width="180"
				 >
				  </el-table-column>
				  <el-table-column
					  prop="blog_createtime"
					  label=""
				  >
				  </el-table-column>
				  <el-table-column
					  prop="blog_commentcount"
					  label=""
				  >
				  </el-table-column>
				  <el-table-column
					  prop="blog_viewcount"
					  label=""
				  >  
				  </el-table-column>
				  <el-table-column
				  	  prop="blog_type"
				  	  label=""
				  >  
				  </el-table-column>
				  <el-table-column
				  	  prop="isHot"
				  	  label=""
				  >  
					<template slot-scope="scope">
						<el-switch
							v-model="scope.row.isHot == 0 ? false: true"
							active-color="#0000FF"
							inactive-color="#CCCCCC"
							@change="changeStatus(scope.row)"
						>
					    </el-switch>
					</template>
				  </el-table-column>
				  <el-table-column label="">
				        <template slot-scope="scope">
				            <el-button
				              size="mini"
							  class="btn"
				              @click="handleEdit(scope.$index, scope.row)"></el-button>
							
							<el-popconfirm
							  style="margin-left: 10px;"
							  title=""
							  @confirm="handleDelete(scope.$index, scope.row)"
							>
								<el-button
								  size="mini"
								  class="btn"
								  type="danger"
								  slot="reference"
								  ></el-button>
							</el-popconfirm>
							
				        </template>
				  </el-table-column>
			</el-table>
			<el-pagination
			  class="box-pagination"
			  @size-change="handleSizeChange"
			  @current-change="handleCurrentChange"
			  :current-page="currentPage"
			  :page-sizes="pageSizesChoose"
			  :page-size="pageSize"
			  layout="total, sizes, prev, pager, next, jumper"
			  :total="tableDataTotal">
			</el-pagination>
			
			<el-dialog
			   title=""
			   :visible.sync="formShow"
			   width="30%"
			   :before-close="handleClose"
			   >
				 <el-form ref="ruleForm" :model="form" :rules="rules" class="form" label-width="80px">
				   <el-form-item label="" prop="blog_title">
					   <el-input v-model="form.blog_title" style="width: 300px;"></el-input>
				   </el-form-item>
				   <el-form-item label="" prop="blog_preface">
					   <el-input v-model="form.blog_preface" style="width: 300px;"></el-input>
				   </el-form-item>
				   <el-form-item label="" prop="blog_commentcount">
					   <el-input v-model.number="form.blog_commentcount" style="width: 300px;"></el-input>
				   </el-form-item>
				   <el-form-item label="" prop="blog_viewcount">
				   		<el-input v-model.number="form.blog_viewcount" style="width: 300px;"></el-input>
				   </el-form-item>
				   <el-form-item label="" prop="blog_type">
						<el-select v-model="form.blog_type" placeholder="">
							<el-option label="" value=""></el-option>
							<el-option label="" value=""></el-option>
							<el-option label="" value=""></el-option>
							<el-option label="" value=""></el-option>
						</el-select>
				   </el-form-item>
				 </el-form>
				 <span slot="footer" class="dialog-footer">
				    <el-button @click="cancelForm('ruleForm')"> </el-button>
				    <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
				 </span>
			</el-dialog>
			
			<el-dialog
			   title=""
			   :visible.sync="formShow1"
			   width="30%"
			   :before-close="handleClose1"
			   >
			   <el-form ref="ruleForm1" :model="form1" :rules="rules1" class="form1" label-width="80px">
				 <el-form-item label="" prop="blog_title">
					<el-input v-model="form1.blog_title" style="width: 300px;" :disabled="true"></el-input>
				 </el-form-item>
				 <el-form-item label="" prop="blog_preface">
				 	<el-input v-model="form1.blog_preface" style="width: 300px;"></el-input>
				 </el-form-item>
				 <el-form-item label="" prop="blog_commentcount">
				 	<el-input v-model.number="form1.blog_commentcount" style="width: 300px;"></el-input>
				 </el-form-item>
				 <el-form-item label="" prop="blog_viewcount">
				 	<el-input v-model.number="form1.blog_viewcount" style="width: 300px;"></el-input>
				 </el-form-item>
				 <el-form-item label="" prop="blog_type">
					<el-select v-model="form1.blog_type" placeholder="">
						<el-option label="" value=""></el-option>
						<el-option label="" value=""></el-option>
						<el-option label="" value=""></el-option>
						<el-option label="" value=""></el-option>
					</el-select>
				 </el-form-item>
			   </el-form>
			   <span slot="footer" class="dialog-footer">
			      <el-button @click="cancelForm1('ruleForm1')"> </el-button>
			      <el-button type="primary" @click="submitForm1('ruleForm1')"> </el-button>
			   </span>
		    </el-dialog>
			
		</el-card>
	</div>
</template>

<script>
	export default{
		name: 'Detail',
		data(){
			var checkTitle = async (rule,value,callback) => {
				let params = {
					blog_title: value
				}
				const res = await this.$http.get('/blogAdminDetail/isBlogAdminDetailTitle',{params: params})
				// console.log(res)
				if(!value){
					return callback(new Error(''))
				}else if(value.length < 1 || value.length > 30){
					return callback(new Error(' 1  30 '))
				}else if(res.data == true){
					return callback(new Error(''))
				}else{
					callback()
				}
			}
			return{
				tableData: [],
				title: '',
				currentPage: 1,
				pageSizesChoose: [1,2,5,10],
				pageSize: 5,
				tableDataTotal: 0,
				formShow: false,
				formShow1: false,
				form:{
					blog_title: '',
					blog_preface: '',
					blog_commentcount: 0,
					blog_viewcount: 0,
					blog_type: ''
				},
				form1:{
					blog_title: '',
					blog_preface: '',
					blog_commentcount: 0,
					blog_viewcount: 0,
					blog_type: ''
				},
				rules:{
					blog_title:[
						{validator: checkTitle,trigger: 'blur'}
						// { min: 1, max: 30, message: ' 1  30 ', trigger: 'blur' }	
					],
					blog_preface:[
						{required: true, message: '',trigger: 'blur'},
						{ min: 3, max: 60, message: ' 3  60 ', trigger: 'blur' }	
					],
					blog_commentcount:[
						{required: true, message: '',trigger: 'blur'},
						{type: 'number', message: ''}
					],
					blog_viewcount:[
						{required: true, message: '',trigger: 'blur'},
						{type: 'number', message: ''}
					],
					blog_type:[
						{required: true, message: '',trigger: 'change'},
					]
				},
				rules1:{
					blog_title:[
						{required: true, message: '',trigger: 'blur'},
						{ min: 1, max: 30, message: ' 1  30 ', trigger: 'blur' }	
					],
					blog_preface:[
						{required: true, message: '',trigger: 'blur'},
						{ min: 3, max: 60, message: ' 3  60 ', trigger: 'blur' }	
					],
					blog_commentcount:[
						{required: true, message: '',trigger: 'blur'},
						{type: 'number', message: ''}
					],
					blog_viewcount:[
						{required: true, message: '',trigger: 'blur'},
						{type: 'number', message: ''}
					],
					blog_type:[
						{required: true, message: '',trigger: 'change'},
					]
				}
			}
		},
		async created() {
			const res = await this.$http.get('/blogAdminDetail/selectBlogAdminDetailTotal')
			this.tableDataTotal = res.data
			
			let params = {
				currentPage: this.currentPage,
				pageSize: this.pageSize
			}
			const res1 = await this.$http.get('/blogAdminDetail/selectBlogAdminDetailByPage',{params: params})
			this.tableData = res1.data
		},
		methods:{
			handleEdit(index, row){
				// console.log(index,row)
				console.log(row.blog_commentcount,row.blog_viewcount)
				this.formShow1 = true
				this.form1.blog_title = row.blog_title
				this.form1.blog_preface = row.blog_preface
				this.form1.blog_commentcount = row.blog_commentcount
				this.form1.blog_viewcount = row.blog_viewcount
				this.form1.blog_type = row.blog_type
				
				this.form1.id = row.id
			},
			async handleDelete(index, row){
				// console.log(index,row)
				// console.log(row.id)
				let params = {
					id: row.id
				}
				//  Navicat blog_contentblog_detail_idblog_detailid
				// CASCADE
				const res = await this.$http.get('/blogAdminDetail/deleteBlogAdminDetail',{params: params})
				if(res.status == 200){
					this.$message.success('')
				}
				location.reload()
			},
			addBlogDetail(){
				this.formShow = true
			},
			async search(){
				//console.log('search')
				if(this.title == ''){
					this.$message.error('')
					return 
				}
				let params = {
					blog_title: this.title
				}
				const res = await this.$http.get('/blogAdminDetail/selectBlogAdminDetailByTitle',{params:params})
				if(res.status == 200){
					this.tableData = res.data
					this.tableDataTotal = res.data.length
					this.currentPage = 1
					this.pageSize = 1
					this.pageSizesChoose = [1]
				}
			},
			// 
			async handleSizeChange(pageSize){
				this.pageSize = pageSize
				let params = {
					currentPage: this.currentPage,
					pageSize: this.pageSize
				}
				const res = await this.$http.get('/blogAdminDetail/selectBlogAdminDetailByPage',{params: params})
				this.tableData = res.data
			},
			// 
			async handleCurrentChange(currentPage){
				this.currentPage = currentPage
				let params = {
					currentPage: this.currentPage,
					pageSize: this.pageSize
				}
				const res = await this.$http.get('/blogAdminDetail/selectBlogAdminDetailByPage',{params: params})
				this.tableData = res.data
			},
			cancelForm(formName){
				this.$refs[formName].resetFields()
				this.formShow = false
			},
			handleClose(){
				this.$refs.ruleForm.resetFields()
				this.formShow = false
			},
			submitForm(formName){
				this.$refs[formName].validate(async (valid) => {
					if(valid){
						// console.log(this.form)
						const res = await this.$http.post('/blogAdminDetail/insertBlogAdminDetail',this.form)
						if(res.status == 200){
							this.$message.success('')
							this.$refs[formName].resetFields()
							this.formShow = false
						}
						location.reload()
					}else{
						this.$message.error('')
						return false;
					}
				})
			},
			handleClose1(){
				this.$refs.ruleForm1.resetFields()
				this.formShow1 = false
			},
			cancelForm1(formName){
				this.$refs[formName].resetFields()
				this.formShow1 = false
			},
			submitForm1(formName){
				this.$refs[formName].validate(async (valid) => {
					if(valid){
						const res = await this.$http.post('/blogAdminDetail/updateBlogAdminDetail',this.form1)
						if(res.status == 200){
							this.$message.success('')
							this.$refs[formName].resetFields()
							this.formShow1 = false
						}
						location.reload()
					}else{
						this.$message.error('')
						return false;
					}
				})
			},
			async changeStatus(row){
				// console.log(row.isHot)
				if(row.isHot == 1){
					row.isHot = 0
					let params = {
						id: row.id,
						isHot: 0
					}
					const res = await this.$http.get('/blogAdminDetail/updateBlogAdminDetailIsHot',{params: params})
					// console.log(res)
				}else{
					row.isHot = 1
					let params = {
						id: row.id,
						isHot: 1
					}
					const res = await this.$http.get('/blogAdminDetail/updateBlogAdminDetailIsHot',{params: params})
					// console.log(res)
				}
			}
		}
	}
</script>

<style lang="less" scoped>
	.detail-main{
		// height: 100%;
	}
	.el-breadcrumb{
		// background-color: yellow;
		width: 500px;
		margin-left: 45px;
		margin-top: 20px;
	}
	.box-card{
		width: 1200px;
		margin: 30px auto;
		.box-table{
			margin-top: 20px;
			/deep/.cell{
				padding-left: 0;
				padding-right: 0;
			}
			.btn{
				// width: 60px;
			}
		}
		.box-pagination{
			margin-top: 20px;
		}
		
		/deep/ .el-dialog__footer{
			// margin-top: -20px;
		}
	}
</style>