<template>
	<div>
		<el-breadcrumb separator-class="el-icon-arrow-right">
		  <el-breadcrumb-item :to="{ path: '/admin/main' }"></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<el-card class="box-card">
			<el-popconfirm
				style="margin-left: 10px;"
				title=""
				@confirm="clearComment"
			>
				<el-button type="primary" size="mini" slot="reference" style="height: 40px; font-size: 13px;"></el-button>
			</el-popconfirm>
			
			<el-input v-model.trim="comment" placeholder="" style="width: 220px; margin-left: 775px;"></el-input>
			<el-button type="primary" icon="el-icon-search" style="margin-left: 10px;" @click="search"></el-button>
			
			<el-table
			    :data="tableData"
				class="box-table"
			    border
			    style="width: 100%"
			>
				<el-table-column
				    label="#"
					type="index"
					width="50"
				>
				</el-table-column>
				<el-table-column
					prop="blog_title"
					label=""
					width="180"
				>
				</el-table-column>
				<el-table-column
					prop="blog_comment"
					label=""
					width="180"
				>
				</el-table-column>
				<el-table-column
					prop="nickname"
					label=""
					width="180"
				>
				</el-table-column>
				<el-table-column
					prop="create_time"
					label=""
				>
				</el-table-column>
				<el-table-column label="" >
					<template slot-scope="scope">
						<!-- <el-button
						  size="mini"
						  @click="handleEdit(scope.$index, scope.row)"></el-button> -->
						  <!-- style="margin-left: 20px;" -->
						<el-popconfirm
							style="margin-left: 10px;"
							title=""
							@confirm="handleDelete(scope.$index, scope.row)"
						>
							<el-button
							  size="mini"
							  type="danger"
							  slot="reference"></el-button>
						</el-popconfirm>
					</template>
				</el-table-column>
			</el-table>
			<el-pagination
			  class="box-pagination"
			  @size-change="handleSizeChange"
			  @current-change="handleCurrentChange"
			  :current-page="currentPage"
			  :page-sizes="pageSizesChoose"
			  :page-size="pageSize"
			  layout="total, sizes, prev, pager, next, jumper"
			  :total="tableDataTotal">
			</el-pagination>
			
		</el-card>
	</div>	
</template>

<script>
	export default{
		name: 'Comment',
		async created() {
			const res = await this.$http.get('/blogAdminComment/blogAdminCommentCount')
			this.tableDataTotal = res.data
			
			let params = {
				currentPage: this.currentPage,
				pageSize: this.pageSize
			}
			const res1 = await this.$http.get('/blogAdminComment/selectBlogAdminCommentByPage',{params: params})
			this.tableData = res1.data
		},
		data(){
			return {
				comment: '',
				tableData:[
					{
						blog_title: '',
						blog_comment: '1',
						nickname: '1',
						create_time: '2022-10-23 11:03:49'
					},
					{
						blog_title: 'xm',
						blog_comment: 'haha',
						nickname: 	'xiaoming',
						create_time: '2022-11-7 11:46:00'
					},
				],
				tableDataTotal: 0,
				// 
				currentPage: 1,
				// 
				pageSize: 5,
				// 
				pageSizesChoose: [1, 2, 5, 10]
			}
		},
		methods:{
			async clearComment(){
				// const res = await this.$http.delete('/blogAdminComment/deleteBlogAdminCommentAll')
				// console.log(res)
				
				// 
				const res = await this.$http.delete('/blogAdminComment/deleteBlogAdminCommentAll')
				// console.log('')
				if(res.status == 200){
					await this.$http.get('/blogAdminComment/updateBlogAdminCommentAll')
					this.$message.success('')
					location.reload()
				}
				
			},
			async search(){
				// console.log('' + this.comment)
				if(this.comment == ''){
					this.$message.error('')
					return
				}
				let params = {
					keyword: this.comment
				}
				const res = await this.$http.get('/blogAdminComment/selectBlogAdminCommentByKeyWord',{params: params})
				if(res.status == 200){
					this.tableDataTotal = res.data.length
					this.currentPage = 1
					this.pageSize = 1
					let params1 = {
						keyword: this.comment,
						currentPage: this.currentPage,
						pageSize: this.pageSize
					}
					const res1 = await this.$http.get('/blogAdminComment/selectBlogAdminCommentByKeyWordAndPage',{params: params1})
					this.tableData = res1.data
				}
			},
			// 
			// handleEdit(index, row) {
			//     console.log(index, row);
			// },
			// 
			async handleDelete(index, row) {
				
				// sql
				// 
				
				// 1. 
				let params = {
					blog_detail_id: row.blog_detail_id
				}
				const res = await this.$http.get('/blogAdminComment/updateBlogAdminDetailCommentCount',{params: params})
				if(res.status == 200){
					// 2. 
					let params1 = {
						id: row.id
					}
					this.$http.get('/blogAdminComment/deleteBlogAdminCommentById',{params: params1})
				}
				location.reload()
				
				// 1. 
			 //    let params = {
				// 	id: row.id
				// }
				// const res = await this.$http.get('/blogAdminComment/deleteBlogAdminCommentById',{params: params})
				// if(res.status == 200){
				// 	// 2. 
				// 	let params1 = {
				// 		blog_detail_id: row.blog_detail_id
				// 	}
				// 	const res1 = await this.$http.get('/blogAdminComment/updateBlogAdminDetailCommentCount',{params: params1})
				// 	// console.log(res1)
				// 	// let obj = JSON.parse(sessionStorage.getItem('paramsData'))
				// 	// obj.blog_commentcount--;
				// 	// sessionStorage.setItem('paramsData',JSON.stringify(obj))
				// 	this.$message.success('')
				// }
				// location.reload()
			},
			// 
			
			//  
			
			async handleSizeChange(val) {
				this.pageSize = val
				if(this.comment == ''){
					let params = {
						currentPage: this.currentPage,
						pageSize: this.pageSize
					}
					const res1 = await this.$http.get('/blogAdminComment/selectBlogAdminCommentByPage',{params: params})
					this.tableData = res1.data
				}else{
					let params1 = {
						keyword: this.comment,
						currentPage: this.currentPage,
						pageSize: this.pageSize
					}
					const res1 = await this.$http.get('/blogAdminComment/selectBlogAdminCommentByKeyWordAndPage',{params: params1})
					this.tableData = res1.data
				}
			},
			// 
			async handleCurrentChange(val) {
				this.currentPage = val
				if(this.comment == ''){
					let params = {
						currentPage: this.currentPage,
						pageSize: this.pageSize
					}
					const res1 = await this.$http.get('/blogAdminComment/selectBlogAdminCommentByPage',{params: params})
					this.tableData = res1.data
				}else{
					let params1 = {
						keyword: this.comment,
						currentPage: this.currentPage,
						pageSize: this.pageSize
					}
					const res1 = await this.$http.get('/blogAdminComment/selectBlogAdminCommentByKeyWordAndPage',{params: params1})
					this.tableData = res1.data
				}
				
			}
		}
	}
</script>

<style lang="less" scoped>
	.el-breadcrumb{
		// background-color: yellow;
		width: 500px;
		margin-left: 45px;
		margin-top: 20px;
	}
	.box-card{
		width: 1200px;
		// height: 600px;
		margin: 30px auto;
		.box-table{
			margin-top: 20px;
		}
		.box-pagination{
			margin-top: 20px;
		}
	}
</style>